.signUp{
    height: 92vh;
    background-color: #1773EA;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form{
    background-color: #a48e8e;
    padding: 30px;
    border: 1px solid rgb(129, 121, 121);
    border-radius: 1px;
    
}

.loginPara{
    color: rgba(var(142,142,142),1);
    font-size: 17px;
    font-weight: 600;
    line-height: 20px;
    margin: 0px 40px 20px 40px;
}

input{
    padding: 5px;
    margin: 3px 0px;
    outline: none;
    font-size: 16px;
    width: 100%;
    box-sizing: border-box;
}

.form > div{
    width: 80%;
    margin: auto;
}

#submit-btn{
    background-color: #1773EA;
    border: none;
    border-radius: 3px;
    color: white;
    font-weight: bold;
    padding: 5px 10px;
    width: 80%;
    cursor: pointer;
    margin-top: 8px;
}

.form2{
    background-color: #fff;
    padding: 30px;
    margin-top: 5px;
    border: 1px solid rgb(129, 121, 121);
    border-radius: 1px;
    }